.p-works__card__image--RaiseTech-sns-banner{
    //右下にroleを固定
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    background:url(../img/RaiseTech-sns-banner-thumbnail.png) left top/cover;
    margin-bottom: calc(32/1440*100vw);
    width:calc(280/1440*100vw);
    height:calc(280/1440*100vw);
    border: 1px #000 solid;
    &:hover{
        background:url(../img/RaiseTech-sns-banner-thumbnail-zoom.png) left top/cover;
        transition: linear .1s;
    }
}